@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  searchTerm: String;
  constructor() { }

  ngOnInit() {
    this.searchTerm = '';
  }
  onSearch() {
    console.log(this.searchTerm);
  }

}



